/* Modal Overlay with Gradient Effect */

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.7), rgba(50, 50, 50, 0.5));
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    font-family: 'Times New Roman';
}


/* Modal Content with Animation */

.modal-content {
    background-color: #ffffff;
    padding: 30px;
    border-radius: 15px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
    width: 500px;
    position: relative;
    animation: slideFadeIn 0.4s ease-out;
}


/* Modal Heading */

.modal-content h1 {
    text-align: center;
    margin-bottom: 20px;
    font-size: 2rem;
    color: #2c3e50;
    font-weight: 600;
}


/* Close Icon in Top-Right Corner */

.close-icon {
    position: absolute;
    top: 15px;
    right: 15px;
    font-size: 1.5rem;
    /* Slightly larger for visibility */
    color: #e74c3c;
    cursor: pointer;
    transition: color 0.2s ease, transform 0.3s ease, background-color 0.2s ease;
    padding: 5px;
    /* Adds padding for a clickable area */
    border-radius: 95%;
    /* Circular shape */
}

.close-icon:hover {
    color: #ffffff;
    background-color: #c0392b;
    /* Background change on hover */
    transform: scale(1.15);
    /* Slight scale up on hover */
}


/* Applications Table */

.applications-table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0 10px;
    margin-top: 20px;
    font-family: Arial, sans-serif;
}

.applications-table th,
.applications-table td {
    border: none;
    padding: 15px 20px;
    text-align: left;
    background-color: #ecf0f1;
    color: #34495e;
}

.applications-table th {
    background-color: #3498db;
    font-weight: bold;
    font-size: 1.1rem;
    color: #ffffff;
    border-radius: 8px 8px 0 0;
}

.applications-table tr:nth-child(even) td {
    background-color: #f5f9fc;
}

.applications-table tr:hover td {
    background-color: #d0e7f9;
    transition: background-color 0.3s ease;
}


/* Table Cell Shadow for Card-Like Look */

.applications-table td {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
}


/* Additional Styling for Headings and Paragraphs */

h2,
h3 {
    color: #2c3e50;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-bottom: 12px;
}

p {
    color: #555;
    line-height: 1.6;
    font-size: 1.1rem;
}


/* Keyframe for Slide & Fade Animation */

@keyframes slideFadeIn {
    from {
        opacity: 0;
        transform: translateY(-20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}