.upload-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 150px;
    width: 100%;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
    padding: 10px 10px 10px 10px;
}

.upload-button {
    margin-top: 20px;
    padding: 12px 25px;
    background-color: #4CAF50;
    color: white;
    border: none;
    cursor: pointer;
    font-size: 16px;
    border-radius: 5px;
    width: 100%;
    max-width: 150px;
    text-align: center;
    transition: background-color 0.3s ease;
}

.upload-button:hover {
    background-color: #45a049;
}

.upload-status {
    margin-top: 20px;
    font-size: 16px;
    color: #333;
    text-align: center;
}

input[type="file"] {
    margin-top: 20px;
    font-size: 16px;
    cursor: pointer;
    width: 100%;
    max-width: 400px;
    border-radius: 10px 10px 10px 10px;
    border-color: rgb(31, 31, 31);
}

@media (max-width: 768px) {
    .upload-container {
        margin-top: 100px;
        width: 90%;
    }
    .upload-button {
        max-width: 100%;
    }
    input[type="file"] {
        max-width: 100%;
    }
}


/* UploadPage.css */

.success-message {
    color: green;
}

.error-message {
    color: red;
}