.company-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.company-list-items {
    display: flex;
    flex-wrap: wrap;
    /* Allow cards to wrap */
    justify-content: center;
    /* Center the cards */
    width: 100%;
    max-width: 800px;
    /* Adjust to fit your design */
}

.company-card {
    background: #fff;
    /* White background for cards */
    border: 2px solid #ddd;
    /* Light gray solid border */
    border-radius: 12px;
    /* Rounded corners */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    /* Enhanced shadow for depth */
    padding: 20px;
    /* Inner padding for card */
    margin: 15px;
    /* Margin between cards */
    width: 200px;
    /* Set a fixed width for cards */
    text-align: center;
    /* Center text in the card */
    transition: transform 0.3s, box-shadow 0.3s;
}

.company-card:hover {
    transform: scale(1.05);
    /* Slightly enlarge card on hover */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
    /* Enhance shadow on hover */
}

.company-card h3 {
    margin: 0 0 10px;
    /* Margin below the heading */
}

.company-card p {
    margin: 0;
    /* No margin around paragraph */
}